import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { useParams } from "react-router-dom";

import { useDebriefing } from "../DebriefingContext";

import SingleColumnLayout from "../layouts/SingleColumnLayout";
import Tooltip from "./Tooltip";

import styles from "../Cond2Debriefing.module.css";


export default function Review() {
  const { getSlideData, enableNextSlide } = useDebriefing();
  const { stepPath } = useParams();
  const [sp, setSP] = useState(null);

  const [toolTipText, setToolTipText] = useState(null)
  const [myData, setMyData] = useState(null)

  const toggleToolTipText = (text) => {
    return toolTipText ? setToolTipText(null) : setToolTipText(text)
  }

  useEffect(() => {
    // if (!myData) {
      if(!sp || sp !== stepPath) {
        setSP(stepPath)

        setMyData(getSlideData("R", stepPath))
        enableNextSlide("R", stepPath);
    }
  }, [stepPath, sp, myData, getSlideData, enableNextSlide]);

  if (!myData) {
    return <div>loading...</div>
  }

  return (
    <SingleColumnLayout currStep="R" currStepPath={stepPath}>
      <h2>
        <b>R</b>eview what was supposed to happen:
      </h2>
      <div className={classNames(styles.slideshow, styles.card)}>
        <div className={styles['card-body']}>
          <p className={styles['hint-text']}>
            {myData.of[0]} of {myData.of[1]}
          </p>
          {myData.paragraphs?.map((chunks, i) => (
            <p key={i}>
              {chunks.map((chunk, j) => (
                <span key={j}>
                  {chunk.hoverText ? (
                    <Tooltip
                      text={chunk.text}
                      hoverText={chunk.hoverText}
                      toggleToolTip={toggleToolTipText}
                      setToolTip={setToolTipText}
                    />
                  ) : (
                    <span>{chunk.text}</span>
                  )}
                </span>
              ))}
            </p>
          ))}
          {toolTipText && (
            <p
              style={{
                backgroundColor: "black",
                color: "white",
              }}
            >
              {toolTipText}
            </p>
          )}
        </div>
      </div>
    </SingleColumnLayout>
  );
}
