import React, {useState} from "react"
import { Switch } from 'react-router-dom';

import { useFirebaseProject, FirebaseContext} from "@meddbriefer/mdb-firebase"
import { AuthProvider, PrivateRoute } from "@meddbriefer/mdb-auth";
import {
  ObserverContext,
  Scenario,
  UnauthedLayout,
} from "@meddbriefer/observer-ui";

import { GetReview } from "@meddbriefer/cond1-debriefing-ui";
import { DebLog, computeDebriefingTitle, SaveDebriefingModal } from "@meddbriefer/cond2-debriefing-ui";

import { environment } from "./environments/environment";

import Welcome from "./components/Welcome";
import ExperimentSetup from './components/ExperimentSetup';
import NextAssignment from "./components/NextAssignment";
import AnalyzeObservations from "./components/AnalyzeObservations";

import './styles.css';


export default function App() {
    const {auth, db} = useFirebaseProject(environment.firebaseProject)
    const [observer, setObserver] = useState(null)

    return (
      <FirebaseContext.Provider
        value={{
          auth,
          db,
        }}
      >
        <ObserverContext.Provider
          value={{
            classCode: environment.classCode,
          }}
        >
          <AuthProvider>
            <Switch>
              <PrivateRoute
                exact
                path="/debriefing1/:logID"
                authFlowLayout={UnauthedLayout}
              >
                <GetReview next="/next-assignment" />
              </PrivateRoute>
              <PrivateRoute
                path="/debriefing2/:debriefingID"
                authFlowLayout={UnauthedLayout}
              >
                <DebLog 
                  computeTitle={computeDebriefingTitle}
                  saveButtonLabel="Done"
                  saveModal={SaveDebriefingModal}
                  next="/next-assignment"
                />
              </PrivateRoute>
              <PrivateRoute
                path="/scenario/:scenarioId"
                authFlowLayout={UnauthedLayout}
              >
                <Scenario observer={observer} next="/analyze-observations" />
              </PrivateRoute>
              <PrivateRoute
                path="/analyze-observations/:observerLogID"
                authFlowLayout={UnauthedLayout}
                component={AnalyzeObservations}
              />
              <PrivateRoute
                exact
                path="/next-assignment"
                authFlowLayout={UnauthedLayout}
              >
                <NextAssignment observer={observer} setObserver={setObserver} />
              </PrivateRoute>
              <PrivateRoute
                exact
                path="/experiment-setup"
                authFlowLayout={UnauthedLayout}
                component={ExperimentSetup}
              />
              <PrivateRoute
                exact
                path="/"
                authFlowLayout={UnauthedLayout}
                component={Welcome}
              />
            </Switch>
          </AuthProvider>
        </ObserverContext.Provider>
      </FirebaseContext.Provider>
    );
}