import React, { useEffect, useState } from "react";

import { useParams } from "react-router-dom";
import { useDebriefing } from "../DebriefingContext";

import SingleColumnLayout from "../layouts/SingleColumnLayout";
import Card from "../Card";

export default function Define() {
  const { stepPath } = useParams();
  const { getSlideData, enableNextSlide } = useDebriefing();
  const [myData, setMyData] = useState(null);

  useEffect(() => {
    if (!myData) {
      setMyData(getSlideData("D", stepPath));
      enableNextSlide("D", stepPath);
    }
  }, [stepPath, getSlideData, myData, enableNextSlide]);

  if (!myData) {
    return <div>loading...</div>;
  }
  return (
    <SingleColumnLayout 
      currStep="D" 
      currStepPath={stepPath}
    >
      <Card title={myData.title}>
        {myData.text.map((para, idx) => (
          <p key={idx}>{para}</p>
        ))}
      </Card>
    </SingleColumnLayout>
  );
}
