import React from "react"

import { useDebriefing } from "../DebriefingContext"

import OutlineButton from "./OutlineButton"
import OutlineLabel from "./OutlineLabel"
import OutlineChildren from "./OutlineChildren"

export default function HierarchicalLeftNav({currStep, currStepPath, outline, expandedSlides}) {
  const {getPathData} = useDebriefing()

  return (
    <>
      {currStepPath === outline.stepPath ? (
        <OutlineLabel node={outline} depth={0} />
      ) : (
        <OutlineButton
          node={outline}
          currStep={currStep}
          currStepPath={currStepPath}
          depth={0}
        />
      )}
      {getPathData(outline.step, outline.stepPath).rated && 
        <OutlineChildren
          currStep={currStep}
          currStepPath={currStepPath}
          node={outline}
          expandedSlides={expandedSlides}
          depth={0}
        />
      }
    </>
  );
}