import React, { useEffect, useState } from "react";

import { Button } from "reactstrap";

const PLACEHOLDER =
  "There are errors at this step. We will discuss them shortly";

export default function AnalysisMessages({ messages }) {
  const [msgs, setMsgs] = useState(null);
  const [showLess, setShowLess] = useState(false);

  useEffect(() => {
    const tmp = messages.filter((msg) => msg !== "");
    const tmp2 = tmp.length > 0 ? [...tmp] : [PLACEHOLDER];
    setMsgs(tmp2);
    setShowLess(tmp2.length > 1);
  }, [messages]);

  if (!msgs) {
    return <div>calculating</div>;
  }
  if (msgs.length === 0) {
    return <span></span>;
  }
  return (
    <div>
      {showLess ? (
        <>
          <div>{msgs[0]}</div>
          <Button
            color="link"
            size="sm"
            onClick={() => setShowLess(false)}
            style={{
              border: 0,
              padding: 0,
            }}
          >
            show more
          </Button>
        </>
      ) : (
        <>
          {msgs.map((msg, i) => (
            <p 
              key={i}
              style={{
                marginBottom: "8px"
              }}
            >
              {msg}
            </p>
          ))}
          {msgs.length > 1 && (
            <Button
              color="link"
              size="sm"
              onClick={() => setShowLess(true)}
              style={{
                border: 0,
                padding: 0,
              }}
            >
              show less
            </Button>
          )}
        </>
      )}
    </div>
  );
}
