import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

import { useDebriefing } from "../DebriefingContext";

import TwoDivLayout from "../layouts/TwoDivLayout";
import HierarchicalLeftNav from "../outline/HierarchicalLeftNav";

import SequentialIEView from "./SequentialIEView";
import TabbedIEView from "./TabbedIEView";


export default function InvestigateExamine() {
  const { stepPath } = useParams();
  const { getPathData,getSlideData, ieOutline, computeExpandedSlides } = useDebriefing();

  const [sp, setSP] = useState(null)
  const [expandedSlides, setExpandedSlides] = useState(null)
  const [myData, setMyData] = useState(null);
  const [completed, setCompleted] = useState(null);
  const [examined, setExamined] = useState(null)

  useEffect(
    () => { 
      if(!sp || sp !== stepPath) {
        setSP(stepPath)
        // console.log("stepPath set")
        setExpandedSlides(computeExpandedSlides(stepPath));
        const slide = getSlideData("IE", stepPath);
        // console.log(slide)
        setMyData(slide);
        const {completed: _completed, examined: _examined} = getPathData("IE", stepPath)
        setExamined(_examined)
        setCompleted(_completed);
      }
    }, 
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [stepPath]
  )


  if (!myData) {
    return <div>loading...</div>
  }

  return (
    <TwoDivLayout
      currStep="IE"
      currStepPath={stepPath}
      outline={
        <HierarchicalLeftNav
          currStep="IE"
          currStepPath={stepPath}
          outline={ieOutline}
          expandedSlides={expandedSlides}
        />
      }
    >
      {completed 
       ? (
        <TabbedIEView myData={myData} stepPath={stepPath} examined={examined} />
      ) : (
        <SequentialIEView myData={myData} stepPath={stepPath} setCompleted={setCompleted} />
      )}
    </TwoDivLayout>
  );
}
