import React from "react";
import { Button } from "reactstrap";
import classNames from "classnames";

import { useDebriefing } from "../DebriefingContext";

import styles from "../Cond2Debriefing.module.css";

export default function NavButtons({currStep, currStepPath}) {
    const {haveNextSlide, havePrevSlide, nextSlide, prevSlide} = useDebriefing()
    return (
      <>
        <Button
          disabled={!havePrevSlide(currStep, currStepPath)}
          color="primary"
          size="sm"
          className={classNames(styles.prev, styles['nav-btn'])}
          onClick={() => prevSlide(currStep, currStepPath)}
        >
          Back
        </Button>
        <Button
          disabled={!haveNextSlide(currStep, currStepPath)}
          color="primary"
          size="sm"
          className={classNames(styles.next, styles['nav-btn'])}
          onClick={() => nextSlide(currStep, currStepPath)}
        >
          Next
        </Button>
      </>
    );
}