import { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { useFirebase } from "@meddbriefer/mdb-firebase"
import { AuthContext } from '@meddbriefer/mdb-auth'

import { getCond1CommentLog, getAnalysisLog, getObserverLog, getPublishedScenarioObjectByID } from "@meddbriefer/scenario-data"
import { saveCommentLog } from "@meddbriefer/analysis"
import DisplayContent from './DisplayContent'

export default function GetReview({next=null}) {
  const {db} = useFirebase()
  const { userName } = useContext(AuthContext)
  const { logID } = useParams()
  const [ loaded, setLoaded ] = useState(null)
  const [ comments, setComments ] = useState(null)
  const [ actions, setActions ] = useState(null)
  const [ scenario, setScenario ] = useState(null)
  const [ analysisLog, setAnalysisLog ] = useState(null)
  //if there is a comment log for the input id then use that to get the
  //analysis log, otherwise the input logID is an analysis log
 
  const getComment = (index) => comments[index]

  const updateComment = (index, value) => {
    const copy = [...comments]
    copy[index] = value
    setComments(copy)
  }

  const storeComments = (event) => {
    event.preventDefault() // probably unneeded
    // analysisLog.id = undefined
    saveCommentLog(db, analysisLog, logID, comments, userName)
}

// const saveAsCommentLogLocally = () => {
//     analysisLog = saveAsCommentLog(db, analysisLog, comments , userName)
// }


  useEffect(
    () => {
      getCond1CommentLog(db, logID)
        .then(debriefLogData => {
            getAnalysisLog(db,debriefLogData.analysisLogID)
            .then(analysisLogData => {
              setAnalysisLog(analysisLogData)
              setActions(analysisLogData.events)
              setComments(debriefLogData.comments)
              getObserverLog(db,analysisLogData.observerLogID)
              .then(observerLogData => {
                getPublishedScenarioObjectByID(db, observerLogData.scenarioID)
                .then(scenData => {
                  setScenario(scenData)
                  setLoaded(true)
                })
            })
          })
        })
    },
    [db,logID]
  ) 

  


  if (!loaded){
    return (<h3>Loading</h3>)
  }

  return (
    <DisplayContent
      analysisLog={analysisLog}
      actions={actions}
      scenario={scenario}
      getComment={getComment}
      updateComment={updateComment}
      saveComments={storeComments}
      next={next}
    />
  )

}

