import { useEffect, useState } from "react"

import { intvVariant2JSON, SELECTION_TYPES } from "@meddbriefer/scenario-data"
import { useScenario } from "../../contexts/ScenarioContext"

import {difference, logicalAndCorrectness, logicalOrCorrectness, radioCorrectness} from "./util"

import Panel from "../Panel"
import PromptsForm from "./PromptsForm";
import { interventionStates, intvResults, good, bad, neutral } from "@meddbriefer/feedback-data/analysisData"

const FORM_MODES = Object.freeze({
    DISP_NONE: 1,
    DISP_DIST_PROMPTS: 2,
    DISP_PROMPTS: 3
})


export default function IntvForm({intvID}) {
    const {
        currPhase,
        currSubPhase,
        getIntvVariantHistory,
        getTimestamp,
        logIntv,
        scenario,
        setCurrIntv,
        setCurrIntvCat,
        setShowIntvSelection,
    } = useScenario()

    const [form, setForm] = useState(null)
    const [formMode, setFormMode] = useState(FORM_MODES.DISP_NONE)
    const [distPromptState, setDistPromptState] = useState(null);
    const [intvVariant, setIntvVariant] = useState(null)
    const [promptState, setPromptState] = useState(null)

    const submitDistPrompts = () => setIntvVariant(intvVariant2JSON(distPromptState))

    const submitPrompts = (event) => {
        setFormMode(FORM_MODES.DISP_NONE)
        logIntv({
            interventionID: intvID,
            label: form.abbrLabel,
            patientStatus: determineInterventionStatus(),
            type: "intervention",
            intvVariant: intvVariant,
            timestamp: getTimestamp(),
            answers: promptState,
            performedCorrectly: determineInterventionCorrectness(),
            system: form.system || null,
            phase: currPhase,
            subPhase: currSubPhase
        })
        setCurrIntv(null)
       // setCurrIntvCat("")
        setShowIntvSelection(true)
    }

    const determineInterventionStatus = () => {
        let results = intvResults[intvID]
        let status = ""
        if (results){
            let type = results.type
            let statusType = results[scenario.name]
            let typeEntry = interventionStates[type]
            status = typeEntry[statusType]
        }
        return status
    }

    const determinePromptCorrectness = (prompt, intvVariantAnswers) => {
        const selectionType = prompt.selectionType
        const allPromptAnswers = new Set(prompt.answers.map((ans) => ans.id));
        const correctAnswers = new Set(intvVariantAnswers[prompt.id]);
        const incorrectAnswers = difference(allPromptAnswers, correctAnswers)
        const selectedAnswers = new Set(promptState[prompt.id])
        console.log("selectionType", selectionType)
        console.log("all answers", allPromptAnswers)
        console.log("correct answers", correctAnswers)
        console.log("wrong answers", incorrectAnswers)
        console.log("selected answers", selectedAnswers)
        let retVal = false
        switch (selectionType) {
            case SELECTION_TYPES.ONE:
                retVal = radioCorrectness(correctAnswers, incorrectAnswers, selectedAnswers)
                break
            case SELECTION_TYPES.OOM_AND:
                retVal = logicalAndCorrectness(correctAnswers, incorrectAnswers, selectedAnswers)
                break
            case SELECTION_TYPES.OOM_OR:
                retVal = logicalOrCorrectness(correctAnswers, incorrectAnswers, selectedAnswers)
                break
            default:
                console.log("unsupported prompt semantics", selectionType)
                retVal = false
        }
        console.log(`${prompt.id} correctness: ${retVal}`)
        return retVal
    }

    const determineInterventionCorrectness = () => {
        // if there are no correct answers specified for this intervention, return false
        // else ALL prompts must be correct, each of whom's correctness is based off the semantics defined by
        // prompt.selectionType 
        if (! scenario.interventionVariantAnswers[intvID]) {
            // not a single intervention variant associated with answers, this intervention must be
            // contraindicated, non-indicated or something else
            console.log(`${intvID} doesn't have any correct answers associated with it`)
            return false
        }
        if (! scenario.interventionVariantAnswers[intvID][intvVariant]) {
          // while the intervention does have intervention variants, this isn't one of them, so we're
          // treating the interventin as having been performed incorrectly
          console.log("intvVariant not found, treating intervention as having been performed incorrectly")
          return false
        }
        // default to true and if ANY prompt is answered incorrectly, set to false
        let performedCorrectly = true
        const intvVariantAnswers = scenario.interventionVariantAnswers[intvID][intvVariant]
        Object.keys(promptState).forEach(promptID => {
            const prompt = form.prompts.find(pr => pr.id === promptID)
            if (!determinePromptCorrectness(prompt, intvVariantAnswers)) {
                performedCorrectly = false
            }            
        })
        console.log(intvID, performedCorrectly)
        return performedCorrectly
    }


    const getSelected = (id, stateVar) => stateVar[id] || []
    
    const isSelected = (id, value, stateVar) => getSelected(id, stateVar).includes(value);

    const setSelected = (id, value, stateSetter) => {
        stateSetter((prevState) => ({
            ...prevState,
            [id]: value,
        }));
    }

    const getDistPromptSelections = (id) => getSelected(id, distPromptState)
    const getPromptSelections = (id) => getSelected(id, promptState)


    const distPromptOptionIsSelected = (id, value) => isSelected(id, value, distPromptState)
    const promptOptionIsSelected = (id, value) => isSelected(id, value, promptState)

    const setDistPrompt = (id, value) => setSelected(id, value, setDistPromptState)
    const setPrompt = (id, value) => setSelected(id, value, setPromptState)

    // const setDistPromptOptionSelected = (id, value, checked) => setSelected(id, value, checked, distPromptState, setDistPromptState)

    // const setPromptOptionSelected = (id, value, checked) => setSelected(id, value, checked, promptState, setPromptState)

    const notDefaultValue = (val) => {
        let retVal = false
        if (typeof val === "string" && "" !== val) {
            retVal = true
        } else if (typeof val !== "string" && val.length > 0) {
            retVal = true
        }
        return retVal
    }
    
    const formComplete = (formState) => Object.values(formState).every(notDefaultValue)

    const distPromptFormComplete = () => formComplete(distPromptState)
    const promptFormComplete = () => formComplete(promptState)

    // returns mapping of keys to empty array
    const initializeState = (prompts) => {
        const tmp = {}
        prompts.forEach(p => tmp[p.id] = [])
        return tmp
    }

    useEffect(
        // should only run on initial display of form
        () => {
            if (!form) {
                const frm = scenario.getInterventionForm(intvID)
                console.log(frm)
                setForm(frm)
                // initialize the dist prompts form
                setDistPromptState(initializeState(frm.distPrompts))
                if (frm.distPrompts.length > 0) {
                    setFormMode(FORM_MODES.DISP_DIST_PROMPTS)
                } else {
                    setIntvVariant(JSON.stringify({}))
                    setFormMode(FORM_MODES.DISP_PROMPTS);
                }
            }
        }, [intvID, form, scenario]
    )

    useEffect(
        () => {
            if (intvVariant && !promptState) {
                // once the intvVariant has been determined
                //   (whether by the user or automatic - when no alternates to choose from)
                // search the history for the most recent form submision for this intervention & variant,
                //    if found, init the form to those saved prompt answers
                //    else simply initialize empty form
                const hist = getIntvVariantHistory(intvID, intvVariant)
                if (hist.length > 0) {
                    console.log("intvVariant History", hist)
                    const lastVariantLogged = hist.slice(-1)[0]
                    console.log("most recent", lastVariantLogged)
                    setPromptState(lastVariantLogged.answers)
                } else {
                    setPromptState(initializeState(form.prompts))
                }
                setFormMode(FORM_MODES.DISP_PROMPTS)
            }
        }, [intvID, intvVariant, promptState, getIntvVariantHistory, form]
    )

    const displaySubForm = () => {
        switch (formMode) {
          case FORM_MODES.DISP_DIST_PROMPTS:
            return (
              <PromptsForm
                prompts={form.distPrompts}
                isSelected={distPromptOptionIsSelected}
                getSelected={getDistPromptSelections}
                setSelected={setDistPrompt}
                isDisabled={!distPromptFormComplete()}
                onSubmit={submitDistPrompts}
              />
            );
          case FORM_MODES.DISP_PROMPTS:
            if (!promptState) {
              // avoiding a race condition where we're waiting for state to be set which is async
              // and promptState still being undefined
              return <div>loading...</div>;
            }
            return (
              <PromptsForm
                prompts={form.prompts}
                isSelected={promptOptionIsSelected}
                getSelected={getPromptSelections}
                setSelected={setPrompt}
                isDisabled={!promptFormComplete()}
                onSubmit={submitPrompts}
              />
            );
          // some other case
          // reverse prompts stuff - unimplemented
          // {
          //     displaySubmit &&
          //         <
          //         ConfirmInterventionButton
          //     clickHandler = {
          //         confirmIntvHandler
          //     }
          //     enabled = {
          //         form.prompts.length === 0
          //     }
          //     />
          // }
          default:
            return (
              <div>
                Neither distPrompts nor prompts. Not sure what to display
                currently
              </div>
            );
        }
    }


    if (!form) {
        return (<div>processing....</div>)
    } 
    return (
      <Panel
        title={form.abbrLabel}
        centerTitle={true}
        collapsable={false}
        scrollBody={true}
      >
        {displaySubForm()}
      </Panel>
    )
}