import React, {useEffect, useState} from "react";

import { useDebriefing } from "../DebriefingContext";

import UserRating from "./UserRating";
import Analysis from "./Analysis";
import ExamineForm from "./ExamineForm";

import { isAbsent, hasError } from "../../utils";

import styles from "../Cond2Debriefing.module.css";



export default function SequentialIEView({myData, stepPath, setCompleted, initialMode="rate"}) {
    const {enableNextSlide, getPathData, ieSummary, updateIdData, updatePathData} = useDebriefing()
    const [needToExamine, setNeedToExamine] = useState(null);
    const [rated, setRated] = useState(null);
    const [analyzed, setAnalyzed] = useState(null);
    const [examined, setExamined] = useState(null);
    const [mode, setMode] = useState(null);
    const [computeMode, setComputeMode] = useState(false);

    const getMode = () => mode;

      const onRatingComplete = () => {
        setRated(true);
        updatePathData("IE", stepPath, { rated: true });
        setComputeMode(true);
      };

      const onAnalysisComplete = (event) => {
        event.preventDefault();
        setAnalyzed(true);
        updatePathData("IE", stepPath, { analyzed: true });
        setComputeMode(true);
      };

      const onExamineComplete = (examineResponse) => {
        setExamined(true);
        updateIdData(myData.id, { examine: examineResponse });
        updatePathData("IE", stepPath, { examined: true });
        setComputeMode(true);
      };

    useEffect(
      () => {
        const pd = getPathData("IE", stepPath)
        // console.log(pd)
        const {
          rated: _rated,
          analyzed: _analyzed,
          examined: _examined,
        } = pd;

        const need2props = {
          examIfAbs: myData.examineIfAbsent,
          absent: isAbsent(ieSummary, myData.id),
          examIfErrs: myData.examineIfErrors,
          errors: hasError(ieSummary, myData.id),
        };

        const need2 =
          (need2props.examIfAbs && need2props.absent) ||
          (need2props.examIfErrs && need2props.errors);
        console.log("need2Examine", need2props, need2);
        setNeedToExamine(need2);

        setExamined(_examined);
        setAnalyzed(_analyzed);
        setRated(_rated);
        setMode(initialMode);
        setComputeMode(true);
      }, [myData, stepPath, initialMode, ieSummary, getPathData]
    )
      useEffect(() => {
        if (
          !computeMode ||
          rated === null ||
          analyzed === null ||
          examined === null
        ) {
          // console.log(`skipping computation of mode: computeMode: ${computeMode} rated: ${rated} analyzed: ${analyzed} examined: ${examined}`)
        } else {
          let newMode;
          if (examined) {
            newMode = "examine";
            enableNextSlide("IE", stepPath);
          } else if (analyzed) {
            if (needToExamine) {
              newMode = "examine";
            } else {
              newMode = "analysis";
              enableNextSlide("IE", stepPath);
            }
          } else if (rated) {
            newMode = "analysis";
          } else {
            newMode = "rate";
          }

          if (newMode !== getMode()) {
            setMode(newMode);
          }
          setComputeMode(false);
        }
      }, [
        computeMode,
        stepPath,
        rated,
        analyzed,
        examined,
        needToExamine,
        getMode,
        enableNextSlide,
        setCompleted
      ]);


    return (
      <div className={styles["feedback-block"]}>
        {myData.subActions.length > 0 && (
          <>
            {"rate" === mode && (
              <UserRating slide={myData} ratedHandler={onRatingComplete} />
            )}
            {"analysis" === mode && (
              <Analysis
                slide={myData}
                stepPath={stepPath}
                analyzedHandler={onAnalysisComplete}
              />
            )}
            {"examine" === mode && (
              <ExamineForm
                slide={myData}
                stepPath={stepPath}
                prompts={isAbsent(ieSummary, myData.id) ? myData.promptsIfAbsent : myData.promptsIfErrors}
                submitHandler={onExamineComplete}
              />
            )}
          </>
        )}
      </div>
    );
}