export const pushNew = (elt, array) => {
  if (!array.includes(elt)) {
    array.push(elt);
  }
};

export const intersects = (arr1, arr2) => {
    let result = arr1.filter(x => arr2.indexOf(x) !== -1);
    if (result.length > 0){
      return true
    }
    else {return false}
}


