export function trimActions(actions,fields){
  let newActions = []
  actions.forEach (act => {
      newActions.push(trimAction(act,fields))
  })
  return newActions}


export function trimAction(event, fields) {
  
  let newEvent = {}
  //regularize some field names across headers and checklist events
  if (!!event.labelID) {
       newEvent.id = event.labelID}
  else { newEvent.id = event.id }
      
  //now copy over these fields
  fields.forEach(fld => {
      if (!!event[fld]) {
          newEvent[fld] = event[fld]}
  })
  return newEvent
}

export const getPhaseStatus = (statuses) => {
  //for collecting first unique item statuses and then
  //unique phase statuses
  let itemStatuses = [];
  let phaseStatuses = [];
  if (statuses.length === 0) {
    return "absent";
  }

  //get a unique list of item status values
  statuses.forEach((status) => {
    if (status !== "notFound"){
      pushNew(status, itemStatuses);
    }
    /* if (!itemStatuses.includes(status)){
            itemStatuses.push(status)
        } */
  });

  //turn the item status values into phase level status values
  let res;
  for (const [key, val] of Object.entries(phaseStatusList)) {
    res = val.filter((value) => itemStatuses.includes(value));
    //if there is an intersection, save the key (i.e. is unique)
    if (res.length !== 0) pushNew(key, phaseStatuses);
    /* if (!phaseStatuses.includes(key)){
                phaseStatuses.push(key)} */
  }

  if (phaseStatuses.length === 1) {
    if (phaseStatuses[0] === "error"){
      return "errors"}
    else {
      return phaseStatuses[0];}
  } // to be absent or good, all entries must be that value 
  else {
    if (phaseStatuses.length === 2 && phaseStatuses.includes("good") && intersects(phaseStatuses, ["misordered", "misOrdered"])){
      return "misordered"
    }   
    else {
      if (intersects(phaseStatuses, ["misordered", "misorderedErrors", "misorderedError"]) && intersects(phaseStatuses, ["error", "errors", "absent"])){
        return "misorderedErrors"
      }
      else {
        return "errors";}}
  } //otherwise they are not all good to that translates to errors
};

