export const saveAnalysisLog = async (db, obsLog, analysisData, userName) => {
  const {events: actions, requiredPhaseNames} = analysisData

  let metaData = {
    "label": obsLog.label,
    "analyzer": userName,
    "observerLogID": obsLog.id,
    "classCode": obsLog.classCode || "",
    "condition": obsLog.condition || "",
    "scenarioID": obsLog.scenarioID,
    "scenarioName": obsLog.scenarioName,
    "FBfields": buildC2FB(obsLog.scenarioName, requiredPhaseNames),
    /* "constraintsViolated": constraintsViolated,
    "requiredPhaseNames": requiredPhaseNames,*/
    "feedbackTemplates": fbTemplatesDef,
    /* "promptIDs": promptIDs,
    "decisionPromptIDs": decisionPromptIDs,  */
    "timestamp": Date()
    }
  let data = {
    ...metaData,
    ...analysisData
  }

  //data["comments"] = Array(actions.length).fill("")  
    if (!!actions) {
      let newActionsList = [];
      let newObj = {};
      let filterUnknown = false;
      //reporting included for debugging puposes only
      actions.forEach(function (logObj, index) {
        for (let [key, val] of Object.entries(logObj)) {
          if (undefined === val) {
            console.log("filtered out from events; index: ", index, " key: ", key, " value: ", val);
            filterUnknown = true;
          }
        }
      });
      //do the filtering of unknowns
      if (filterUnknown) {
        actions.forEach(function (logObj, index) {
          newObj = _.pickBy(logObj, function (value, key) {
            return !(value === undefined);
          });
          newActionsList.push(newObj);
        });
        data["events"] = newActionsList;
      } 
      else {data["events"] = actions;}
    }
  //console.log("about to save data", data)
  const dataWithID = await saveLog(db, "analyzed-logs", undefined, data)  
  //window.alert(`analyzed-log/${data.label} created`); 
  return dataWithID

}

