export async function saveLog (db, collection, id, data) {
  //console.log("In saveLog")
  var docRef
  if (!!id){
    docRef = db.collection(collection).doc(id);}
  else {
    docRef = db.collection(collection).doc();
    data.id = docRef.id;
  }
  await docRef.set(data);  
  return data
};

