export const getTimestamp = (currentEntry) => {
  if (!currentEntry.timestamp || currentEntry.type === "header") {
    return "";
  }
  if (currentEntry.timestamp === -1) {
    return "\xa0\xa0"; // formatAsMinSecs would convert this to 59:59
  }
  return formatAsMinSecs(currentEntry.timestamp);
}

let unknownItem = {id: "Unknown", label: "Unknown"}

const getItems = (val,itemByID,itemByLabel) => {
  if (val === "Unknown"){
      return unknownItem}
  let items = []
  items = itemByID[val]
  if (!items){
      items = itemByLabel[val]}
  return items
}

export const getItem = (val,itemByID,itemByLabel) => {
  let items = getItems(val,itemByID,itemByLabel)
  if (items){return items[0]}
}