import React, { useContext, useEffect, useRef, useState } from "react";
import { useParams, Redirect } from "react-router-dom";

import { analyzeEvents, saveAnalysisLog } from "@meddbriefer/analysis";
import { AuthContext } from "@meddbriefer/mdb-auth";
import { useFirebase } from "@meddbriefer/mdb-firebase";
import {
  getObserverLog,
  getPublishedScenarioObjectByID,
  createCond1Comments,
  createCond2Debriefing,
} from "@meddbriefer/scenario-data";

export default function AnalyzeObservations() {
  const { db } = useFirebase();
  const { getUserData, saveUserData } = useContext(AuthContext);
  const { observerLogID } = useParams();
  const shouldAnalyze = useRef(true);
      
  const [userCond, setUserCond] = useState(null);
  const [debriefingID, setDebriefingiD] = useState(null);
  const [ready, setReady] = useState(false);

  const analyzeObservations = async () => {
    console.log("analyzeObservations()");
    const obsLog = await getObserverLog(db, observerLogID);
    const scenario = await getPublishedScenarioObjectByID(
      db,
      obsLog.scenarioID
    );
    const userData = await getUserData();
    const completedAssignments = [...userData.completedAssignments];
    // save to the db that this assignment is completed
    completedAssignments.push({
      name: scenario.name,
      publishedScenarioID: obsLog.scenarioID,
      observerLogID: observerLogID,
    });
    await saveUserData("completedAssignments", completedAssignments);
    // so we know what debriefing log to generate and show, if scenario.info.debriefing === true
    setUserCond(userData.condition);

    let debriefID;
    if (scenario.info.debriefing) {
      const analysis = analyzeEvents(scenario, obsLog);
      console.log("analysis", analysis)
      const analysisLog = await saveAnalysisLog(
        db,
        obsLog,
        analysis,
        obsLog.userName
      );
      if ("cond1" === userData.condition) {
        debriefID = await createCond1Comments(db, analysisLog, obsLog.userName);
      } else if ("cond2" === userData.condition) {
        debriefID = await createCond2Debriefing(db, analysisLog, obsLog.userName);
      }
    }
    setDebriefingiD(debriefID);
  };

  useEffect(() => {
      // if (db && observerLogID && !ready) {
      if (shouldAnalyze.current) {
        shouldAnalyze.current = false
        analyzeObservations()
          .then(() => setReady(true))
          .catch((error) => console.error(error));
      }
      // }
    }, 
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  )

  if (!ready) {
    return <div>analyzing logs</div>;
  }
  if (debriefingID) {
    if (userCond === "cond1") {
      return <Redirect to={`/debriefing1/${debriefingID}`} />;
    } else if (userCond === "cond2") {
      // currently unreachable
      return <Redirect to={`/debriefing2/${debriefingID}`} />;
    }
  }
  return <Redirect to="/next-assignment" />;
}
