import React, {useState} from "react";
import {TabContent, TabPane, Nav, NavItem, NavLink} from "reactstrap"
import classNames from "classnames";

import { useDebriefing } from "../DebriefingContext";
import Analysis from "./Analysis";
import ExamineForm from "./ExamineForm";

import { isAbsent } from "../../utils";

import styles from "../Cond2Debriefing.module.css"

export default function TabbedIEView({myData, stepPath, examined}) {
    const {ieSummary} = useDebriefing()
    const [activeTab, setActiveTab] = useState("feedback")

    const setTab = (tabID) => setActiveTab(tabID)

    return (
        <div className={styles['feedback-block']}>
            <Nav tabs>
                <NavItem>
                    <NavLink
                        className={classNames({active: activeTab === "feedback"})}
                        onClick={() => setTab("feedback")}
                    >
                        Feedback
                    </NavLink>
                </NavItem>
                <NavItem>
                    <NavLink
                        className={classNames({active: activeTab === "examine"})}
                        onClick={() => setTab("examine")}
                        disabled={!examined}
                    >
                        Your Reflection
                    </NavLink>
                </NavItem>
            </Nav>
            <TabContent activeTab={activeTab}>
                <TabPane tabId="feedback">
                    <Analysis
                        slide={myData}
                        stepPath={stepPath}
                    />
                </TabPane>
                <TabPane tabId="examine">
                    <ExamineForm
                        slide={myData}
                        stepPath={stepPath}
                        prompts={isAbsent(ieSummary, myData.id) ? myData.promptsIfAbsent : myData.promptsIfErrors}
                    />
                </TabPane>
            </TabContent>
        </div>
    )
}