import React from "react"
import { Button } from "reactstrap";
import { useHistory } from "react-router-dom";

import { MDBModal } from "@meddbriefer/mdb-ui";

export default function SaveModal({ show, dismiss }) {
  let history = useHistory()
  const submitHandler = (event) => {
    event.preventDefault();
    console.log("I'm done clicked");
    history.push("/")
  };

  return (
    <MDBModal
      title="Are you finished with this debriefing?"
      show={show}
      dismiss={dismiss}
    >
      <div className="container">
        <div className="row">
          <div className="col-12">
            <p>
              <span className="text-danger">WARNING: </span>
              <span>
                Clicking "I'm Done" will not allow you to view the debriefing
                any longer. Don't click on it unless you are truly done with
                this debriefing.
              </span>
            </p>
          </div>
        </div>
        <div className="row">
          <div className="col-12">
            <Button color="danger" size="lg" onClick={submitHandler}>
              I'm Done
            </Button>
            <Button color="primary" size="lg" onClick={dismiss}>
              Cancel
            </Button>
          </div>
        </div>
      </div>
    </MDBModal>
  );
}
