import React, { useEffect, useState, useContext } from "react";
import { Redirect } from "react-router-dom";
import { useFirebase } from "@meddbriefer/mdb-firebase";
import { AuthContext } from "@meddbriefer/mdb-auth";

export default function ExperimentSetup() {
  const {auth, db} = useFirebase()
  const {getUserData, currentUser, saveUserData} = useContext(AuthContext)
  const [ready, setReady] = useState(false)

  const getNumCondParticipants = (condition) => {
    return db
      .collection("users")
      .where("classCode", "==", "trial1")
      .where("condition", "==", condition)
      .get()
      .then(snapshot => snapshot.size)
      .catch(error => {
        console.error(error)
        return error
      })
  }

  const assignCondition = async () => {
    const numCond1Participants = await getNumCondParticipants("cond1")
    const numCond2Participants = await getNumCondParticipants("cond2")
    console.log(`cond1: ${numCond1Participants} cond2: ${numCond2Participants}`)
    if ("development" === process.env['NODE_ENV']) {
      return "cond2"
    } else {
    // disable for now. cond2 component not ready yet. also can't test until I have some trial1 cond1 users
    // if (numCond1Participants > numCond2Participants) {
    //   return "cond2"
    // } else if (numCond2Participants > numCond1Participants) {
    //   return "cond1"
    // } else {
      return "cond1"
    // }
    }
  }

  useEffect(
    () => {
      const performParticipantSetup = async () => {
        const userData = await getUserData()
        let cond = userData.condition
        if (!cond) {
          cond = await assignCondition()
          console.log("assigned user to ", cond)
          await saveUserData("condition", cond)
        }
        if (!userData.completedAssignments) {
          await saveUserData("completedAssignments", [])
        }
        return cond
      }

      if (auth && db && currentUser && !ready) {
        performParticipantSetup()
          .then((cond) => setReady(true))
          .catch(error => console.error(error))
      }
    }, [auth, db, currentUser, ready, assignCondition, getUserData, saveUserData]
  )

  if (!ready) {
    return (
      <div>
        Performing participant setup for experiment
      </div>
    );
  }
  return (
    <Redirect to="/next-assignment" />
  )
}
