import React, { useEffect, useState } from "react";

import { useParams } from "react-router-dom";

import { useDebriefing } from "../DebriefingContext";

import TwoDivLayout from "../layouts/TwoDivLayout";
import FlatLeftNav from "../outline/FlatLeftNav";

export default function Benchmarks() {
  const { stepPath } = useParams();
  const { getSlideData, computeExpandedSlides, bOutline, enableNextSlide } =
    useDebriefing();
  const [sp, setSP] = useState(null)
  const [myData, setMyData] = useState(null)

  useEffect(
    () => {
      if (stepPath && !sp || sp !== stepPath) {
        setSP(stepPath)
        computeExpandedSlides(stepPath)
        setMyData(getSlideData("B", stepPath));
        enableNextSlide("B", stepPath);
      }
    }, 
    [stepPath, sp, myData, getSlideData, computeExpandedSlides, enableNextSlide]
  );

  if (!myData) {
    return <div>loading...</div>
  }

  return (
    <TwoDivLayout 
        currStep="B" 
        currStepPath={stepPath} 
        outline={
          <FlatLeftNav currStep="B" currStepPath={stepPath} outline={bOutline} />
        }
      >
      <h4>{myData.expectationText}</h4>
      <ol
        style={{
          lineHeight: "1.15",
          paddingLeft: "24px",
        }}
      >
        {myData.benchmarks?.map((bm, bmIdx) => (
          <li key={bmIdx} style={{ paddingTop: "10px" }}>
            <span>{bm.benchmarkText}</span>
            {bm.subBenchmarks.length > 0 && (
              <ol
                style={{
                  lineHeight: "1.15",
                  paddingLeft: "24px",
                }}
              >
                {bm.subBenchmarks.map((sbm, sbmIdx) => (
                    <li 
                        key={sbmIdx}
                        style={{paddingTop: "10px"}}
                    >
                        {sbm}
                    </li>
                ))}
              </ol>
            )}
          </li>
        ))}
      </ol>
    </TwoDivLayout>
  );
}
