export default function Tooltip({
  text,
  hoverText,
  toggleToolTip,
  setToolTip,
}) {
  return (
    <span
        onClick={() => toggleToolTip(hoverText)}
        onMouseEnter={() => setToolTip(hoverText)}
        onMouseLeave={() => setToolTip(null)}
    >
      <span
        style={{
          paddingLeft: "0.25em",
          textDecoration: "underline dotted green",
        }}
      >
        {text}
      </span>
      <span
        className="material-icons-outlined"
        style={{
          paddingRight: "0.25em",
          fontSize: "small",
          verticalAlign: "top"
        }}
      >
        info
      </span>
    </span>
  );
}
