import React from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import { Button } from "reactstrap";

// import { AuthContext } from "@meddbriefer/mdb-auth";
import { MDBNavBar  } from "@meddbriefer/mdb-ui";

import { useDebriefing } from "./DebriefingContext";

import StepTabs from "./header/StepTabs";

import Define from "./d/Define";
import Explain from "./e/Explain";
import Benchmarks from "./b/Benchmarks";
import Review from "./r/Review";
import InvestigateExamine from "./ie/InvestigateExamine"
import Formalize from "./f/Formalize";

import styles from "./Cond2Debriefing.module.css"

function SaveButton({label}) {
  const {allSlidesCompleted, setDone} = useDebriefing()

  const clickHandler = (event) => {
    console.log("save button clicked")
    setDone(true)
    event.target.blur()
  }

  return (
    <Button
      color="primary"
      size="sm"
      disabled={!allSlidesCompleted}
      onClick={clickHandler}
    >
      {label}
    </Button>
  );
}

export default function Cond2Debriefing({ debriefingID, computeTitle, saveButtonLabel, saveModal: SaveModal }) {
  const { allSlidesCompleted, done, setDone, obsLog, scenario } = useDebriefing();

  return (
    <div className={styles['cond2-wrapper']}>
      <MDBNavBar 
        title={computeTitle(obsLog, scenario)} 
        leftNav={<SaveButton label={saveButtonLabel}/>}
      />
      <StepTabs />
      <Switch>
        <Route
          path="/debriefing2/:debriefingID/D/:stepPath"
          component={Define}
        />
        <Route
          path="/debriefing2/:debriefingID/E/:stepPath"
          component={Explain}
        />
        <Route
          path="/debriefing2/:debriefingID/B/:stepPath"
          component={Benchmarks}
        />
        <Route
          path="/debriefing2/:debriefingID/R/:stepPath"
          component={Review}
        />
        <Route
          path="/debriefing2/:debriefingID/IE/:stepPath"
          component={InvestigateExamine}
        />
        <Route
          path="/debriefing2/:debriefingID/F/:stepPath"
          component={Formalize}
        />
        <Route>
          <Redirect to={`/debriefing2/${debriefingID}/D/1`} />
        </Route>
      </Switch>
      <SaveModal show={done} dismiss={() => setDone(false)} />
    </div>
  );
}
