import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

import { useDebriefing } from "../DebriefingContext";

import SingleColumnLayout from "../layouts/SingleColumnLayout";
import Card from "../Card";

export default function Explain() {
    const { stepPath } = useParams();
    const { getSlideData, enableNextSlide } = useDebriefing();
    const [myData, setMyData] = useState(null)
    
    useEffect(
        () => {
            if (!myData) {
                setMyData(getSlideData("E", stepPath));
                enableNextSlide("E", stepPath);
            }
        }, 
        [stepPath, myData, getSlideData, enableNextSlide]
    );

    if (!myData) {
        return <div>loading..</div>
    }

    return (
      <SingleColumnLayout currStep="E" currStepPath={stepPath}>
        <Card title={myData.title} desc={myData.description}>
          <ul>
            {myData.text?.map((item, idx) => (
              <li key={idx}>{item}</li>
            ))}
          </ul>
        </Card>
      </SingleColumnLayout>
    );
}
