import React, { useEffect, useState } from "react"
import { Nav } from "reactstrap"
import { useLocation } from "react-router-dom";

import TopButton from "./TopButton";

import styles from "../Cond2Debriefing.module.css";

export default function StepTabs() {
  let location = useLocation()  
  const [currStep, setCurrStep] = useState(null)

    useEffect(
        () => {
          setCurrStep(location.pathname.split("/")[3])
        }, [location]
    )

    if (!currStep) {
      return <span></span>
    }

    return (
      <header className={styles["main-nav"]}>
          <Nav pills>
            <TopButton step="D" currStep={currStep} label="D" />
            <TopButton
              step="E"
              currStep={currStep}
              label="Learning Objectives"
            />
            <TopButton step="B" currStep={currStep} label="Expected Actions" />
            <TopButton step="R" currStep={currStep} label="Expected Story" />
            <TopButton step="IE" currStep={currStep} label="What did Happen" />
            <TopButton step="F" currStep={currStep} label="Take Home Points" />
          </Nav>
      </header>
    );
}

