import { useState, useEffect, useContext } from 'react';
import { Button, Table } from 'reactstrap';
import { useHistory } from 'react-router-dom';
import { AuthContext } from '@meddbriefer/mdb-auth'
import {
  getAssessmentIcon,
  getColor,
  getFeedback,
  getPhaseFeedback,
  getTimestamp
} from "@meddbriefer/analysis";

import {MDBNavBar} from "@meddbriefer/mdb-ui"


let c2FB
let fbTemplates
let promptIDs

const Feedback = ({value}) => {
    // sts 10/10/22 - currently spitting out a validateDOMnesting warning (hopefully only in dev builds).  
    //  <span> is not a valid child of <tr> (only <td> is)  
    // I tried wrapping with a <td> but it screwed up the formatting, so I'm leaving as-is
    return (
        <span dangerouslySetInnerHTML={{ __html: value }} />
    );
}

export default function DisplayContent({analysisLog, actions, scenario, getComment, updateComment, saveComments, next=null}) {
    const { saveUserData, getUserData } = useContext(AuthContext)
    let history = useHistory()
    const [showCommentColumn, setShowCommentColumn] = useState(false)
    const [showToggleButton, setShowToggleButton] = useState(false)
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [startTime, setStartTime] = useState(Date.now())
    //const [events, setEvents] = useState(analysislog.events);
    const [formattedActions, setFormattedActions] = useState(undefined);
    let training = scenario.info.debriefing


    const doRedirect = async (event) => {
      event.preventDefault() // probably unnecessary
      const userData = await getUserData()
      const timeOnTask = userData.timeOnTask ? [...userData.timeOnTask] : []
      timeOnTask.push({scenario: scenario.name, task: "debriefing", startTime: startTime, endTime: Date.now()})
      console.table(timeOnTask)
      await saveUserData("timeOnTask", timeOnTask)
      history.push(next)
    }

    const toggleComments = () => {
        setShowCommentColumn(!showCommentColumn)
    }

    const formatAnswerGiven = (entry) => {
        let answers = []
        let answer = []
        for (const [prompt, value] of Object.entries(entry.answerDetails)){
            let answerString = String(value.givenAnswer)
            answer = answerString.split(":")
            if (answer.length === 2){
                answers.push({ID: answer[0], answer: answer[1]})
            }
            else {answers.push({ID: promptIDs[prompt], answer: value.givenAnswer})}
        }

        return (
            <p>
            {answers.map((entry, index) =>
                {if (entry.ID){
                    return <span key={index}>
                        <b>{entry.ID}:</b> {entry.answer}
                        <br />
                        <br />
                    </span>}
                else {
                    return <span key={index}>
                        {entry.answer}
                        <br />
                        <br />
                    </span>   
                }}
            )}
            </p>
        )
    }

    
    
    let phaseFBGiven = []
    const dispHeaderRow = (entry, index) => {
        let phaseFB = ""
        let result
        if (entry.id === "assessmentHeader"){
            result = getPhaseFeedback(entry,phaseFBGiven,c2FB)
            phaseFB = result[0]
            phaseFBGiven = result[1]
        }
        if (entry.id ===  "assessmentHeader") {  //|| entry.id === "assessmentSubHeader") 
            return (
                <>
                    <td colSpan="3" className={getColor(entry,fbTemplates)}>
                        <b>{entry.label}</b>
                    </td>
                    {training && <Feedback value={phaseFB} />}
                    {training && showCommentColumn &&
                    <td>
                        <textarea
                            rows="2"
                            cols="50"
                            defaultValue={getComment(index)}
                            onBlur={(event) => saveComment(event, index)}
                            id={`${entry.id}`}
                        />
                    </td>
                }
                </>
            )
        }  else {
            return (
                <td colSpan="10" className={"black"}>
                    {/* <div>
                        <em>&nbsp;&nbsp;&nbsp;Intervention Group: {entry.probLabel}</em>
                    </div> */}
                </td>
            )
        }
    }

   /*  function getLabel(entry) {
        return entry.type === "obtain-vital-sign" 
            ? vitalLabel(entry)
            : regularLabel(entry)
    } */

    function getLabel(entry) {
        return entry.type === "obtain-vital-sign" 
            ? vitalLabel(entry)
            : <>{entry.actionDescription}.  {answerData(entry)}{findingsData(entry)}</>
    } 

    function getIntvLabel(entry){
        let interventions = scenario._interventions
        let intv = interventions.find(me => (entry.vital === me.id))
        return intv.label
    }

    function vitalLabel(entry){
        if (entry.vitalType === "vitals") {
            return (
                <>
                    <b>Requested vital:</b> {entry.vital},
                    <span className="text-muted"><p><b>Found:</b>&nbsp;<i>{entry.value}</i></p></span>
                </>
            )
        }
        if (entry.vitalType === "intervention-status") {
            return (
                <>
                    <b>Requested intervention status for:</b> {getIntvLabel(entry)},
                    <span className="text-muted"><p><b>Found:</b>&nbsp;<i>{entry.value}</i></p></span>
                </>
            )
        }
        if (entry.vitalType === "sample") {
            return (
                <>
                    <b>Requested SAMPLE:</b> {entry.vital},
                    <span className="text-muted"><p><b>Found:</b>&nbsp;<i>{entry.value}</i></p></span>
                </>
            )
        }   
        if (entry.vitalType === "opqrst") {
            return (
                <>
                    <b>Requested OPQRST:</b> {entry.vital},
                    <span className="text-muted"><p><b>Found:</b>&nbsp;<i>{entry.value}</i></p></span>
                </>
            )
        }   
        
    }

    /* function regularLabel(entry) {
        return  (entry.subPhase && entry.type !== "intervention") || entry.type === "decision-option"
            ? <>{entry.actionDescription}.  {answerData(entry)}{findingsData(entry)}</>
            : phaseLabel(entry)
    }


    function phaseLabel(entry){
        return entry.type === "intervention"
            ? <>{entry.actionDescription}. {answerData(entry)} </>
            : <>{entry.actionDescription}.  {findingsData(entry)}</>
    } */

    function findingsData(entry){
        return entry.finding
            ? <span className="text-muted"><p><b>Findings:</b> <i>{entry.finding}</i></p></span>
            : <span></span>
    }

    function answerData(entry){
        return entry.answerDetails
            ?<span className="text-muted">{formatAnswerGiven(entry)}</span>
            :<span></span>
    }


    /*  function saveComment(val) {
        let orig = val.target.defaultValue;
        let comment = val.target.value;
        let entryID = val.target.id;
        if (orig !== comment){
            let entry = formattedActions.find(entry => (entry.id === entryID))
            // if (entry) { entry.comment = comment }}
            const newEntry = Object.assign({}, entry, {comment: comment})
            updateFormattedAction(newEntry)
        }
    }
 */

    

    function saveComment(event, index) {
        const newValue = event.target.value
        console.log(newValue, index)
        updateComment(index, newValue)
    }

    //I couldn't get the alternative to dangerouslySetInnerHTML (props.children) to work
    const dispDataRow = (entry, index) => {
        let feedback = getFeedback(entry, c2FB, fbTemplates)
        return (
            <>
                
                <td>{entry.numericalID}</td>
                <td>{getTimestamp(entry)}</td>
                <td className={getColor(entry,fbTemplates)}>
                    <span className="material-icons-outlined">{getAssessmentIcon(entry,fbTemplates)}</span>
                    {getLabel(entry)}
                </td>
                {training && <td><Feedback value={feedback} /></td>}
                           
                {training && showCommentColumn &&
                    <td>
                        <textarea
                            rows="2"
                            cols="50"
                            defaultValue={getComment(index)}
                            onBlur={(event) => saveComment(event, index)}
                            id={`${entry.id}`}
                        />
                    </td>
                }
            </>
        )
    }

//<td dangerouslySetInnerHTML={{ __html: feedback}} /> 

    /* const updateFormattedAction = (record) => {
        const index = formattedActions.findIndex(rec => rec.id === record.id)
        setFormattedActions([
            ...formattedActions.slice(0, index),
            record,
            ...formattedActions.slice(index+1)
        ])
    } */

    function getNavBarTitle() {
      if (next) {
        return `Analysis of ${scenario.name}`;
      }
      if (showToggleButton) {
        return `Review of ${analysisLog.label}`;
      }
      return `Analysis of ${analysisLog.label}`;
    }

    function getLeftNavButton() {
      return (
        <Button
          color="primary"
          onClick={next === null ? saveComments : doRedirect}
        >
          {next === null ? "Save" : "Done"}
        </Button>
      );
    }

    function displayIt () {
        c2FB = analysisLog.FBfields
        fbTemplates = analysisLog.feedbackTemplates
        promptIDs = analysisLog.promptIDs
        // if (comments){
        setShowCommentColumn(next === null) 
        setShowToggleButton(next === null)       
        // }
        // else {
        //     setShowCommentColumn(false)
        // }
        setFormattedActions(actions)
    }

    useEffect(
        () => {
            displayIt()
    
        // eslint-disable-next-line react-hooks/exhaustive-deps
        }, [] // empty list means this code will only be run on initial render
    )

    if (undefined === formattedActions) {
        return (<h3>processing</h3>)
    }

    return (   
        <>
            {showToggleButton
                ? <MDBNavBar
                        title={getNavBarTitle()}
                        leftNav={getLeftNavButton()}
                        rightNav={
                            <Button
                                color="primary"
                                onClick={toggleComments}
                            >
                                Toggle Comments
                            </Button>
                        }
                    />
                : <MDBNavBar
                    title={getNavBarTitle()}
                    leftNav={getLeftNavButton()}
                />
            }
            <form><div style={{ overflowY: "auto", maxHeight: "700px" }}>
            
                <Table borderless size="sm">
                    <thead>
                        <tr>
                            <th className="table-header"> ID </th>
                            <th className="table-header"> Timestamp </th>
                            <th className="table-header"> Action Description </th>
                            {training && <th className="table-header"> Feedback </th>}
                            {training && showCommentColumn && <th className="table-header"> Comments </th>}
                        </tr>
                    </thead>
                    <tbody className="scroll">
                        {formattedActions.map((currentEntry, index) => (
                            <tr key={index}>
                                {currentEntry.type === "header"
                                    ? dispHeaderRow(currentEntry, index)
                                    : dispDataRow(currentEntry, index)
                                }
                            </tr>
                        ))}
                    </tbody>
                </Table>
            </div>
        </form>
        </>
    )
}

