import React from "react";
import {Button} from "reactstrap"

import { useDebriefing } from "../DebriefingContext";

import { capitalizeFirstLetter, lowerCaseFirstLetter, getRequiredSubActions } from "../../utils";


export default function UserRating({slide, ratedHandler}) {
  const { getUserRating, setUserRating } = useDebriefing();

  const handleRatingChange = (event) => {
    const { id, checked } = event.target;
    setUserRating(id, checked);
  };

  return (
    <>
      <p style={{ fontSize: "1rem" }}>
        Click on what you think you completed successfully.
      </p>
      <p>
        <b>
          Did you <i>{lowerCaseFirstLetter(slide.label)}</i>, including:
        </b>
      </p>
      <div>
        {getRequiredSubActions(slide).map((subAction, idx) => (
          <div key={subAction.id}>
            <input
              type="checkbox"
              id={subAction.id}
              checked={getUserRating(subAction.id) === true}
              onChange={handleRatingChange}
            />
            <label
              htmlFor={subAction.id}
              style={{
                display: "inline",
                paddingLeft: "4px",
              }}
            >
              {capitalizeFirstLetter(subAction.label)}
            </label>
          </div>
        ))}
        <br/>
        <Button size="sm" color="primary" onClick={ratedHandler}>
          Show Feedback
        </Button>
      </div>
    </>
  );
}
