import React, { useContext, useEffect, useState } from "react";
import { Redirect, useHistory } from "react-router-dom";
import { Button } from "reactstrap";
// Table, 
import { AuthContext } from "@meddbriefer/mdb-auth";
import { useFirebase } from "@meddbriefer/mdb-firebase";
import {MDBNavBar} from "@meddbriefer/mdb-ui"

import { useObserverContext } from "@meddbriefer/observer-ui"

function SetObserverForm({observer, setObserver, setDoRedirect}) {
  
  const handleSubmit = (event) => {
    event.preventDefault()
    const {observerEmail} = event.target.elements
    setObserver(observerEmail.value)
    setDoRedirect(true)
  }

  return (
    <form onSubmit={handleSubmit}> 
      <label htmlFor="observerEmail">Enter your Email:</label>
      <input
        type="email"
        id="observerEmail"
        defaultValue={observer}
        required
      />
      <br />
      And click &nbsp;
      <Button type="submit" color="primary" size="sm">
        Proceed
      </Button>
      &nbsp; to move on to the next scenario.
      <br />
    </form>
  );
}

export default function NextAssignment({observer, setObserver}) {
  let history = useHistory()
  const { auth, db } = useFirebase()
  const { getUserData, signOutUser } = useContext(AuthContext)
  const { classCode } = useObserverContext()
  const [assignments, setAssignments] = useState(null)
  const [nextAssignment, setNextAssignment] = useState(null)
  const [ready, setReady] = useState(false)
  const [doRedirect, setDoRedirect] = useState(false)


  const handleLogout = (event) => {
    setTimeout(() => {
      signOutUser().then(() => window.location.reload())
    }, 500)
    history.push("/")
  }

  useEffect(
    () => {
      const getNextAssignment = async () => {
        // console.log("class_code", classCode);
        const userData = await getUserData();
        // console.log("userData", userData);
        const completedScenarioIDs = userData.completedAssignments.map(assignment => assignment.publishedScenarioID)
        // console.log("completedScenarioIDs:", completedScenarioIDs)
        const classCodeSnapshot = await db
          .collection("class-codes")
          .where("name", "==", classCode)
          .limit(1)
          .get()
        let tasks = classCodeSnapshot.docs[0].data().assignments
        // console.log("assignments", assignments);
        // const remainingAssignments = assignments.filter(assignment => !(completedScenarioIDs.includes(assignment.publishedScenarioID)))
        tasks = tasks.map(assignment => {
          return Object.assign({}, assignment, {completed: completedScenarioIDs.includes(assignment.publishedScenarioID)})
        })

        // console.table(tasks)
        setAssignments(tasks)
        const remainingAssignments = tasks.filter(assignment => !assignment.completed)
        if (remainingAssignments.length > 0) {
          setNextAssignment(remainingAssignments[0])
        }
      }

      if (auth && db && classCode && !ready) {
        getNextAssignment()
          .then(() => setReady(true))
          .catch((error) => console.error(error));
      }
  }, [auth, db, classCode, ready, getUserData, assignments]);

  if (!ready) {
    return <div>determining next assignment</div>;
  }
  if (doRedirect) {
    return <Redirect to={`/scenario/${nextAssignment.publishedScenarioID}`} />; 
  }

  if (nextAssignment) {
  
    return (
      <>
        <MDBNavBar title="Trial1" />

        {/* <p>Your progress so far:</p>
        <Table size="sm" striped={true}>
          <thead>
            <tr>
              <th>Scenario</th>
              <th>Completed</th>
            </tr>
          </thead>
          <tbody>
            {assignments.map((assign, idx) => (
              <tr key={idx}>
                <td>{assign.name}</td>
                <td>{assign.completed && "X"}</td>
              </tr>
            ))}
          </tbody>
        </Table> */}
        <br />
        <br />
        <h3>Student Instructions</h3>
        <p>
          Please hand this tablet to the person who will be checking off your
          stated actions during the scenario.
        </p>

        <h3>Observer Instructions</h3>
        <div>
          If the student wishes to perform the next scenario:
          <ul style={{ listStyleType: "disc" }}>
            <li>
              in a <i>later</i> session: &nbsp;
              <Button
                type="button"
                color="primary"
                size="sm"
                onClick={handleLogout}
              >
                End Session
              </Button>
            </li>
            <li>
              <i>now</i>:
              <br />
              <SetObserverForm
                scenario={nextAssignment}
                observer={observer}
                setObserver={setObserver}
                setDoRedirect={setDoRedirect}
              />
            </li>
          </ul>
        </div>
      </>
    );
  }
  return (
    <>
      <MDBNavBar title="Trial1" />
      <br />
      <br/>
      <p>
        You have completed all of the scenarios. Thank you for testing our software.
      </p>
      <p>
        The next step is to take a post-test, which will allow the study team to get a sense of how much you learned. 
        Please let the session monitor know that you are ready for the post test.
      </p>
      <div>
        <Button type="button" color="primary" size="sm" onClick={handleLogout}>
          End Session
        </Button>
      </div>
    </>
  );
}
