import React from "react"
import { NavItem, NavLink } from "reactstrap";
import classNames from "classnames";

import { useDebriefing } from '../DebriefingContext';


export default function TopButton({ step, currStep, label }) {
    const { goStepStepPath, stepIsEnabled } = useDebriefing();
    
    const clickHandler = () => goStepStepPath(step, "1")
    
    return (
      <NavItem>
        <NavLink
          className={classNames({active: step === currStep})}
          onClick={clickHandler}
          disabled={!stepIsEnabled(step)}
          // style={{
          //   fontSize: "2.25em",
          //   padding: "0 20px"
          // }}
        >
          {label}
        </NavLink>
      </NavItem> 
    )
 } 
