import React from "react";
import { Button } from "reactstrap";
import classNames from "classnames";

import { useDebriefing } from "../DebriefingContext";

import styles from "../Cond2Debriefing.module.css"

export default function ExamineForm({ slide, stepPath, prompts, submitHandler }) {
  const { getIdData, getPathData } = useDebriefing();

  const handleSubmit = (event) => {
    event.preventDefault()
    const {examine} = event.target.elements
    submitHandler(examine.value)
  };

  return (
    <div
      className={classNames(styles["feedback-form"], styles["feedback-block"])}
    >
      <p>
        <b>
          Let's reflect on: <i>{slide.label}</i>
        </b>
      </p>

      {slide.type?.toLowerCase() === "required" ? (
        <p>What did you do instead? What do you think led to this error?</p>
      ) : (
        <p>Why did you perform this action?</p>
      )}

      {prompts && (
        <>
          <p>
            <b>For example:</b>
          </p>
          <ul>
            {prompts.split(";").map((prompt, i) => (
              <li key={i}>{prompt}</li>
            ))}
          </ul>
        </>
      )}

      <form onSubmit={handleSubmit}>
        <textarea
          id="examine"
          cols={80}
          placeholder="Enter your response here..."
          defaultValue={getIdData(slide.id).examine}
          disabled={getPathData("IE", stepPath).examined}
        />
        <br />
        <Button
          type="submit"
          size="sm"
          color="primary"
          disabled={getPathData("IE", stepPath).examined}
        >
          Submit
        </Button>
      </form>
    </div>
  );
}
