import React, { useEffect, useState } from "react";
import { Button } from "reactstrap";
import { useParams } from "react-router-dom";

import { useDebriefing } from "../DebriefingContext";

import SingleColumnLayout from "../layouts/SingleColumnLayout";
import Card from "../Card";
 
export default function Formalize() {
  const { stepPath } = useParams();
  const { allSlidesCompleted, getSlideData, getIdData, setAllSlidesCompleted, updateIdData } = useDebriefing();
  const [myData, setMyData] = useState(null);

  const submitHandler = (event) => {
    event.preventDefault()
    const {response} = event.target.elements
    console.log(response.value)
    updateIdData('formalize', {response: response.value})
    setAllSlidesCompleted(true)
  }

  useEffect(() => {
    if (!myData) {
      setMyData(getSlideData("F", stepPath));
    }
  }, [stepPath, getSlideData, myData]);

  if (!myData) {
    return <div>loading...</div>;
  }

  return (
    <SingleColumnLayout currStep="F" currStepPath={stepPath}>
      <Card title={myData.title} desc={myData.heading}>
        <h4>{myData.questions}</h4>
        {allSlidesCompleted ? (
          <>
            <h5>You said:</h5>
            <p>{getIdData("formalize").response}</p>
          </>
        ) : (
          <form onSubmit={submitHandler}>
            <textarea
              id="response"
              cols={80}
              rows={4}
              defaultValue={getIdData("formalize").response}
              required
            />

            <br />
            <Button type="submit" color="primary">
              Submit
            </Button>
          </form>
        )}
      </Card>
    </SingleColumnLayout>
  );
}
