import React from "react";
import { Table, Button } from "reactstrap";

import { useDebriefing } from "../DebriefingContext";

import AnalysisMessages from "./AnalysisMessages";
import FeedbackIcon from "./FeedbackIcon";

import styles from "../Cond2Debriefing.module.css";

import { getRequiredSubActions, isGood, isAbsent, hasError, isOutOfOrder } from "../../utils";

export default function Analysis({ slide, stepPath, analyzedHandler }) {
  const { getPathData, getUserRating, ieSummary } = useDebriefing();

  const nonRequiredActions = () => {
    return slide.subActions.filter( (e) =>
      e.type?.toLowerCase() !== "required" &&
      e.log_status &&
      e.log_status !== "notFound"
    )
  }

  return (
    <>
      <h4>
        Feedback based on your recorded actions:
        <i> {slide.label}</i>
      </h4>
      <div
        className={styles["feedback-list"]}
        style={{
          maxHeight: "460px",
          overflowY: "auto",
        }}
      >
        <Table size="sm" striped={true} bordered={true}>
          <thead
            style={{
              height: "28px",
              backgroundColor: "white",
              position: "sticky",
              top: 0,
            }}
          >
            <tr>
              <th>Action</th>
              <th>Your Rating</th>
              <th>System Rating</th>
              <th>Feedback</th>
            </tr>
          </thead>
          <tbody>
            {getRequiredSubActions(slide).map((sa, idx) => (
              <tr key={idx}>
                <td>{sa.label}</td>
                <td>
                  {getUserRating(sa.id) && <i className="bi-check-lg"></i>}{" "}
                </td>
                <td>
                  <FeedbackIcon isGood={isGood(ieSummary, sa.id)} />
                </td>
                <td>
                  {/* 
                  at most one of these should occur as they are mutually exclusive
                  */}
                  {hasError(ieSummary, sa.id) && (
                    <AnalysisMessages messages={sa.feedbackErrors} />
                  )}
                  {isAbsent(ieSummary, sa.id) && (
                    <AnalysisMessages messages={sa.feedbackAbsent} />
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </Table>
      </div>
      {nonRequiredActions().length > 0 &&
        <div>
          <h5>The following steps were not indicated</h5>
          {nonRequiredActions().map(nra => 
          <p>
            {nra.feedbackErrors}
          </p>  
          )}
        </div>
      }
      {isOutOfOrder(ieSummary, slide.id) && (
        <div>
          <h5>The following steps were performed out of order:</h5>
          {slide.feedbackOutOfOrder.map((para, idx) => (
            <p key={idx}>{para}</p>
          ))}
        </div>
      )}
      <Button
        size="sm"
        color="primary"
        onClick={analyzedHandler}
        disabled={getPathData("IE", stepPath).analyzed}
      >
        Ok
      </Button>
    </>
  );
}
