import React from "react"

import OutlineButton from "./OutlineButton"
import OutlineLabel from "./OutlineLabel"


export default function FlatLeftNav({currStep, currStepPath, outline}) {
    return (
        <>
        <h4>{outline.heading}</h4>
        <ul
            style={{
                paddingLeft: "4px",
                marginBottom: "4px"
            }}
        >
            {outline.children.map((child, idx) =>
             <li 
                key={idx}
                style={{
                    marginBottom: "0.5rem"
                }}
             >
                {currStepPath === child.stepPath
                    ?<OutlineLabel node={child} />
                    : <OutlineButton node={child} currStep={currStep} currStepPath={currStepPath}/>
                }
             </li>
            )}
        </ul>
    </>
    )
}